export function scroollByClass(className: string) {
    if(className !== null && className !== "") {
        document.getElementsByClassName(className)[0].scrollIntoView(
            { behavior: "smooth" }
        );
    }else{
        console.log("Invalid element with class: " + className);
    }
}

export function scroollById(idName: string) {
    if(idName !== null && idName !== "") {
        document.getElementById(idName)?.scrollIntoView(
            { behavior: "smooth" }
        );
    }else{
        console.log("Invalid element with id: " + idName);
    }
}

export const isMobile = () => {
    const toMatch = [
        /Android/i,
        /webOS/i,
        /iPhone/i,
        /iPad/i,
        /iPod/i,
        /BlackBerry/i,
        /Windows Phone/i
    ];
    
    return toMatch.some((toMatchItem) => {
        return navigator.userAgent.match(toMatchItem);
    });
}