import { Box, Grid, Tooltip } from '@mui/material';
import { useState } from 'react';
import { Item } from '../Item/Item';
import { backEnd, frameworksBackEnd } from './items_back_end';
import { databases } from './items_db';
import { frameworksFrontEnd, frontEnd } from './items_front_end';
import { tdd } from './items_tdd';
import { ide } from './items_ide';
import { managers } from './items_managers';
import { tools } from './items_tools';
import { servers } from './items_server';
import ModalHardSkill from './ModalHardSkill';

import './styles.scss';

const cardSkills = [
    {
        title: 'Front-end',
        itens: frontEnd,
    },
    {
        title: 'Front-end Frameworks',
        itens: frameworksFrontEnd,
    },
    {
        title: 'Databases',
        itens: databases,
    },
    {
        title: 'Back-end',
        itens: backEnd,
    },
    {
        title: 'Back-end Frameworks',
        itens: frameworksBackEnd,
    },
    {
        title: 'Tools',
        itens: tools,
    },
    {
        title: 'Managers',
        itens: managers,
    },
    {
        title: 'TDD',
        tooltip: 'Test Driven Development',
        itens: tdd,
    },
    {
        title: 'Server',
        tooltip: '',
        itens: servers,
    }
];

export default function HardSkills(props: any) {
    const [hardSkill, setHardSkill] = useState(null);

    return (
        <Box id="box-hard-skills">
            <h1 translate="no">Hard Skills</h1>
            <Grid container spacing={1}>
                {cardSkills.map((card : any) => {
                    return (
                        <Grid key={card.title} item xs={12} md={4} xl={4}>
                            <Item className="item-card-hard-skill">
                                {card.tooltip ? (
                                    <p translate="no">
                                    <Tooltip
                                        title={card.tooltip} placement="top">
                                        <span>{card.title}</span>
                                    </Tooltip>
                                    </p>
                                ) : (
                                    <p translate="no">{card.title}</p>
                                )}
                                <div className="list-items">
                                    {card.itens.map((item : any) => {
                                        return (
                                            <Tooltip disableFocusListener disableTouchListener
                                                key={item.name} title={item.name} placement="top">
                                                <div className="item-img-title-hard-kill">
                                                    <img onClick={() => setHardSkill(item)} src={item.icon} alt={item.name} />
                                                    {item.showName === true ? (
                                                        <span onClick={() => setHardSkill(item)} >{item.name}</span>
                                                        ) : ( 
                                                        <></>
                                                    )}
                                                </div>
                                            </Tooltip>
                                        );
                                    })}
                                </div>
                            </Item>
                        </Grid>
                    );
                })}
            </Grid>
            <ModalHardSkill hardSkill={hardSkill} setHardSkill={setHardSkill} 
                            language={props.language} />
        </Box>
    )
}