import { LanguageService } from '../../services/LanguageService';
import { languageKeys } from './language';

import './styles.scss';

export default function Formation(props : any) {
    const languageService = new LanguageService(languageKeys, props.language);

    return (
        <>
            <h1>{languageService.getKey('formation')}</h1>
            <p>{languageService.getKey('mba-management-projects')}</p>
            <p>{languageService.getKey('software-engineering-specialist')}</p>
            <p>{languageService.getKey('bachelor-of-computer-science')}</p>
        </>
    )
}