import Box from '@mui/material/Box';
import { Tooltip } from '@mui/material';
import imgProfile from '../../assets/img/profile.png';
import imgQrCode from '../../assets/img/QRCode.svg';
import { languageKeys } from './language';
import { LanguageService } from '../../services/LanguageService';
import './styles.scss';
import { itemsSocials } from './itemsSocials';
import { isMobile } from '../../services/UtilServices';

export default function Bio(props : any) {
    const languageService = new LanguageService(languageKeys, props.language);

    return (
        <Box id="box-bio">
            <h1>Aquiles Oliveira</h1>
            <h3 className="highlight">{languageService.getKey('software-engineer')}</h3>
            <img className="image-profile" src={imgProfile} alt="" />
            <h3 className="highlight">{languageService.getKey('line-locale')}</h3>
            <div id="socials-container">
                {itemsSocials.map((social : any) => {
                    return (
                        <Tooltip key={social.name} title={social.name} placement="top">
                            <a href={social.url} target="_blank" rel="noopener noreferrer">
                                <img src={social.icon} alt={social.name} />
                            </a>
                        </Tooltip>
                    );
                })}
            </div>
            <div id="email-container">
                <a href="mailto:aquiles_lopes@outlook.com" target="_blank" rel="noopener noreferrer">
                    oliveira.aquiles@outlook.com
                </a>
            </div>
            {isMobile() ? (
                <span></span>
            ) : (
                <div id="qrcode">
                    <span>{languageService.getKey('see-on-your-mobile')}</span>
                    <img src={imgQrCode} alt="QR Code - Page Aquiles Oliveira" />
                </div>
            )}
        </Box>
    )
};