import { Skeleton } from '@mui/material';

interface Language {
    key: string;
    pt_br: string;
    en_uk: string;
}

export class LanguageService {
    keys: Language[];
    language: string;

    constructor(listKeys: Language[], language: string) {
        this.keys = listKeys;

        this.language = language;
    }

    getKey(key: string): any {
        if(this.language !== 'loading'){
            const obj = this.keys.find(x => x.key === key);
            var value = null;
            if(this.language === 'pt_br') {
                value = obj?.pt_br;
            }else {
                value = obj?.en_uk;
            }
            return value !== null && value !== undefined ? value : '';
        }else{
            return <Skeleton variant="text" width={(key.length * 10) + 'px'} />;
        }
    }
}