import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Backdrop from '@mui/material/Backdrop';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import { Stack } from '@mui/material';
import { LanguageService } from '../../services/LanguageService';
import { languageKeys } from './language';

const style = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

export default function ModalHardSkill(props : any) {
  const languageService = new LanguageService(languageKeys, props.language);
  const [openModal, setOpenModal] = React.useState(false);

  React.useEffect(() => { 
    if(openModal === false && props.hardSkill !== null){
        setOpenModal(true);
    }

    if(openModal && props.hardSkill === null){
        setOpenModal(false);
    }
  }, [props.hardSkill]);

  return (
    <div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={openModal}
        onClose={() => props.setHardSkill(null)}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 1000,
        }}
      >
        <Fade in={openModal}>
        <Box sx={style}>
          { props.hardSkill !== null ? (
            <>
                <div className="modal-skill">
                    <img src={props.hardSkill.icon} 
                        alt={props.hardSkill.name} />
                    {props.hardSkill.showName === true ? (
                      <Typography variant="h4" component="h4">
                       {props.hardSkill.name}
                      </Typography>
                    ) : ( 
                      <></>
                    )}
                </div>
                <div>
                    <Typography variant="body1" component="p">
                        {props.hardSkill.url.replace('https://', '').replace('http://', '')}
                    </Typography>
                </div>
                <footer className="footer-modal-skill">
                    <Stack direction="row" spacing={2}>
                        <Button target="_blank" rel="noopener noreferrer" variant="outlined" 
                                href={props.hardSkill.url}>
                            {languageService.getKey('see-site')}
                        </Button>
                        <Button onClick={() => props.setHardSkill(null)}
                                color="error" variant="outlined" >
                            {languageService.getKey('close')}
                        </Button>
                    </Stack>
                </footer>
            </>
            ) : <span></span>
          }
        </Box>
        </Fade>
      </Modal>
    </div>
  );
}
