import * as React from 'react';
import Box from '@mui/material/Box';
import Backdrop from '@mui/material/Backdrop';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import imgQrCode from '../../assets/img/QRCode.svg';
import './styles.scss';
import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
import imgProfile from '../../assets/img/profile.png';
import { isMobile } from '../../services/UtilServices';

const style = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

export default function ModalQrCode() {
  const [openModal, setOpenModal] = React.useState(false);

  return (
    <>
      <Stack direction="row" spacing={1} onClick={() => setOpenModal(isMobile())}>
        <Avatar alt="" src={imgProfile} />
        <h3>Aquiles Lopes</h3>
      </Stack>
      <Modal
              aria-labelledby="transition-modal-title"
              aria-describedby="transition-modal-description"
              open={openModal}
              onClose={() => setOpenModal(false)}
              closeAfterTransition
              BackdropComponent={Backdrop}
              BackdropProps={{
                timeout: 1000,
              }} 
          >
          <Fade in={openModal}>
            <Box className="box-modal" sx={style}>
                 <img className="img-qrcode" src={imgQrCode} alt="QrCode" />
            </Box>
          </Fade>
      </Modal>
    </>
  );
}
