import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import Container from '@mui/material/Container';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';

import TranslateIcon from '@mui/icons-material/Translate';
import image_brazil from '../../assets/img/brazil.svg';
import image_united_kingdom from '../../assets/img/united_kingdom.svg';

import './styles.scss';
import { toast } from 'react-toastify';
import ModalQrCode from './ModalQrCode';

export default function MenuBar(props: any) {
  const language = props.language != null ? props.language : 'en_uk';
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [status, setStatus] = React.useState(false);

  const handleOpenMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  React.useEffect(() => {
      const handleScroll = () => {
          window.scrollY > 150 ? setStatus(true) : setStatus(false);
      };

      window.addEventListener('scroll', handleScroll);

      return () => {
      window.removeEventListener('scroll', handleScroll);
      };
  }, []);

  function handleChangeLanguage(languageSelected: string) {
    if(language !== languageSelected){
      toast.info('Changing language', {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      
      props.setLanguage('loading');
      setAnchorEl(null);

      setTimeout(function () { 
        props.setLanguage(languageSelected);
      }, 2000);
    }else{
      setAnchorEl(null);
    }
  }

  return (
    <AppBar id="app-bar" position="sticky">
      <Container maxWidth="xl">
        <Toolbar id="menu-bar-toolbar" disableGutters>
      
          <Box sx={{ flexGrow: 0 }}>
            {status ? (
              <ModalQrCode />
            ) : null}
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Change the language">
              <IconButton id="btn-icon-language" onClick={handleOpenMenu}>
                <TranslateIcon />
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorEl)}
              onClose={handleCloseMenu}
            >
            <MenuItem key="en_uk" disabled={language === 'en_uk'} onClick={() => handleChangeLanguage('en_uk')}>
              <img className="image-language" src={image_united_kingdom} alt="English (en_uk)" />
              <Typography textAlign="center">English</Typography>
            </MenuItem>
            <MenuItem key="pt_br" disabled={language === 'pt_br'} onClick={() => handleChangeLanguage('pt_br')}>
              <img className="image-language" src={image_brazil} alt="Portuguese (pt_br)" />
              <Typography textAlign="center">Portuguese</Typography>
            </MenuItem>
            </Menu>
          </Box>

        </Toolbar>
      </Container>
    </AppBar>
  );
};

